import { handleArticle } from "api";
import { memo, useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

type articleDetail = {
  key: number;
  title: string;
  content: string[];
};
const ArticleDetail = () => {
  const location = useLocation();
  const url = location.pathname.split("article/");

  const [article, setArticle] = useState<articleDetail>({
    key: 0,
    title: "",
    content: [],
  });
  useEffect(() => {
    (async function () {
      const result = await handleArticle(url[0] + "article?key=" + url[1]);

      if (result && result.data.article) {
        setArticle(result.data.article);
      }
    })();
  }, []);
  return (
    <>
      <h2>{article?.title}</h2>
      {article?.content.map((item, index) => {
        return <h4 key={article.key + index}>{index + 1 + "„ÄÅ" + item}</h4>;
      })}
    </>
  );
};

export default memo(ArticleDetail);
